@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&subset=latin-ext");a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,canvas,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video{margin:0;padding:0;border:0;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:'';content:none}table{border-collapse:collapse;border-spacing:0}html{font-size:62.5%;font-family:'Roboto', Arial, sans-serif}body{background:#233142}.header{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;width:100%;height:100vh}.carusel{position:relative;width:60%;height:480px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;overflow:hidden}.carusel__testimonials{width:100%;position:absolute;left:0;display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-pack:start;justify-content:flex-start;-ms-flex-align:center;align-items:center;transition:0.2s linear}.carusel__testimonial{min-width:100%;height:400px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;align-items:center}.carusel__image{margin-top:50px;border-radius:50%;border:5px solid #1e2a39;box-shadow:0px 0px 56px -4px rgba(0,0,0,0.75)}.carusel__description{width:80%;margin:30px 0;text-align:center;color:#e3e2e2;font-size:1.5rem;font-weight:300;line-height:20px}.carusel__sign{color:#ffffff;font-size:1.8rem;font-style:italic}.carusel__prev,.carusel__next{cursor:pointer;position:absolute;padding:25px;border-radius:50%;opacity:0.2;transition:opacity 0.4s linear}.carusel__prev:hover,.carusel__next:hover{opacity:0.8}.carusel__prev{background:#1b242f url("../img/left.png") no-repeat center;left:0}.carusel__next{background:#1b242f url("../img/right.png") no-repeat center;right:0}.carusel__bubbles{position:absolute;bottom:0px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center}.carusel__bubble{cursor:pointer;width:10px;height:10px;background:#2a3c52;margin:5px;border:2px solid #293a50;border-radius:50%;transition:0.2s}.carusel__bubble--active{background:#233142}.carusel__bubble:hover{background:#233142}@media (max-width: 1000px){.carusel{width:95%;height:480px}.carusel__image{width:150px}}
